import React, { Component } from 'react';
import './Abacus.css';
import Bead from './Bead';
import { Consumer } from "./AbacusContext";



class AbacusTest extends Component {

    render() {

        return (
            <Consumer>
                {context => (

                    <div 
                        className={"abacus" + context.abacusState.length} 
                        id="abacus"
                        onTouchStart={(e) => context.actions.dragStart(e)}
                        onTouchMove={(e) => context.actions.drag(e)}
                        onTouchEnd={(e) => context.actions.dragEnd(e)}

                        onMouseDown={(e) => context.actions.dragStart(e)}
                        onMouseMove={(e) => context.actions.drag(e)}
                        onMouseUp={(e) => context.actions.dragEnd(e)}
                    >
                        <div className="button_holder">
                            <div className="somecass"
                            onMouseDown={(e) => context.actions.resetStart(e)}  
                            onMouseUp={(e) => context.actions.resetEnd(e)}
                            id="pressarea">
                                <div className="reset" id="reset"></div>
                            </div>
                        </div>
                        <div className="upperedge edgefillhor">
                           
                        </div>
                        {context.abacusState.map((column, i) => (

                            <React.Fragment key={"column_" + i}>

                                <div className={"uppercontainer" + i} id="uc1">
                                    <Bead
                                        column={i}
                                        row={0}
                                    />
                                </div>
                                <div className="separator sepfill"></div>
                                <div className={"bottomcontainer" + i}>
                                    <div className="beadspacer"></div>
                                    <Bead
                                        column={i}
                                        row={1}
                                    />
                                    <Bead
                                        column={i}
                                        row={2}
                                    />
                                    <Bead
                                        column={i}
                                        row={3}
                                    />
                                    <Bead
                                        column={i}
                                        row={4}
                                    />
                                </div>
                            </React.Fragment>
                        ))}
                        <div className="bottomedge edgefillhor"><p className="value">{context.value}</p></div>
                        <div className="rightedge edgefillvert"></div>
                    </div>
                )}
            </Consumer>
        )
    }
}

export default AbacusTest


